<template>
  <vs-sidebar
    relative
    v-model="active"
    :open.sync="openSync"
    color="success"
    @input="linkto"
  >
    <vs-sidebar-item
      class="focus:outline-none"
      :id="item.name"
      v-for="(item, i) in get_menu"
      :key="i"
    >
      <template #icon>
        <i :class="item.icon"></i>
      </template>

      {{ item.text }}
    </vs-sidebar-item>

    <!--  -->
    <template #footer>
      <vs-col class="text-center">
        <vs-avatar class=" cursor-pointer" @click="exit">
          <i class="fas fa-sign-out-alt text-red-500"></i>
        </vs-avatar>
      </vs-col>
    </template>
  </vs-sidebar>
</template>

<script>
export default {
  props: {
    sideBarActive: {
      type: Boolean,
      default: false,
    },
  },

  watch: {
    sideBarActive(newValue) {
      this.openSync = newValue;
    },

    openSync(newValue) {
      this.$emit("sync", newValue);
    },
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },

    get_menu() {
      return this.$store.getters["menu/get_menu_by_user_type"](
        this.get_auth.group
      );
    },
  },

  data() {
    return {
      active: "home",
      items: [
        {
          name: "/home",
          text: "Inicio",
          icon: "fas fa-home",
          link: "/home",
        },

        {
          name: "/user",
          text: "Usuarios",
          icon: "fas fa-user",
          link: "/user",
        },

        {
          name: "asignacion_sillas",
          text: "Asignacion Sillas",
          icon: "fas fa-chair",
        },

        {
          name: "vacantes",
          text: "Vacantes",
          icon: "fas fa-stream",
        },

        {
          name: "periodos",
          text: "Periodos",
          icon: "fas fa-calendar-alt",
        },

        {
          name: "modalidades",
          text: "Modalidades",
          icon: "fas fa-id-card",
        },

        {
          name: "requisitos",
          text: "Requisitos",
          icon: "fas fa-clipboard-list",
        },

        {
          name: "notas_postulante",
          text: "Notas Postulante",
          icon: "fas fa-list-alt",
        },

        {
          name: "postulantes",
          text: "Postulantes",
          icon: "fas fa-users",
        },

        {
          name: "nuevo_postulante",
          text: "Nuevo Postulante",
          icon: "fas fa-user-plus",
        },

        {
          name: "gratuidad",
          text: "Gratuidad",
          icon: "fas fa-star",
        },

        {
          name: "reinscripcion",
          text: "Reinscripcion",
          icon: "fas fa-exchange-alt",
        },

        {
          name: "entrega_prospecto",
          text: "Entrega Prospecto",
          icon: "fas fa-file-alt",
        },
      ],
      openSync: true,
    };
  },

  methods: {
    linkto(e) {
      if (this.$route.path != e) {
        this.$emit("tolink");
        this.$router.push(e);
      }
    },

    exit() {
      this.$store.dispatch("auth/clear_auth");
      this.$router.push("/login");
    },
  },
};
</script>

<style lang="scss" scoped></style>
