<template>
  <div class=" bg-white flex flex-col">
    <header-layout
      class="h-full"
      :sideBarActive="sideBarActive"
      @sideBarActiveEvent="sideBarActiveEvent"
    />

    <div class=" w-full flex">
      <side-bar-layout
        :class="{ hidden: !sideBarActive }"
        :sideBarActive="sideBarActive"
        @tolink="tolinkEvent"
        @sync="SyncEvent"
      />
      <content-layout
        :class="{ absolute: get_screen_text == 'md' || !sideBarActive }"
      />
    </div>
  </div>
</template>

<script>
import ContentLayout from "./ContentLayout.vue";
import HeaderLayout from "./HeaderLayout.vue";
import SideBarLayout from "./SideBarLayout.vue";
export default {
  components: { HeaderLayout, SideBarLayout, ContentLayout },

  data() {
    return {
      sideBarActive: true,
    };
  },

  watch: {
    get_screen_text(newValue) {
      if (newValue == "md" || newValue == "sm") this.sideBarActive = false;
      else this.sideBarActive = true;
    },
  },

  computed: {
    get_screen_text() {
      return this.$store.getters["tools/get_screen_text"];
    },
  },

  mounted() {
    if (this.get_screen_text == "md" || this.get_screen_text == "sm")
      this.sideBarActive = false;
    else this.sideBarActive = true;
  },

  methods: {
    tolinkEvent() {
      if (this.get_screen_text == "md" || this.get_screen_text == "sm") {
        this.sideBarActive = false;
      }
    },

    sideBarActiveEvent() {
      this.sideBarActive = !this.sideBarActive;
    },

    SyncEvent(e) {
      this.sideBarActive = e;
    },
  },
};
</script>

<style lang="scss" scoped></style>
